.loader {
  width: 48px;
  height: 48px;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: 1s;
}

.breed-select {
  width: 200px;
  height: 25px;
}

.error {
  color: #3a0d0d;
}

.cat-info {
  gap: 45px;
  margin-top: 25px;
  display: flex;
}

.textInfo {
  max-width: 600px;
  font-size: large;
}

.hidden, .loading-breed-select, .loading-cat-info, .error {
  display: none;
}

